<template>
  <div>
    <el-form
      ref="loginForm"
      :model="form"
      :rules="rules"
      label-width="80px"
      class="login-box"
    >
      <h3 class="login-title">游客注册</h3>
      <el-form-item label="账号" prop="username">
        <el-input
          type="text"
          placeholder="请输入账号"
          v-model="form.username"
        />
      </el-form-item>
      <el-form-item label="电话" prop="tel">
        <el-input
          type="text"
          placeholder="请输入电话"
          v-model="form.tel"
        />
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          placeholder="请输入密码"
          v-model="form.password"
        />
      </el-form-item>
      <el-form-item label="密码" prop="checkPassword">
        <el-input
          type="password"
          placeholder="请再次输入密码"
          v-model="form.checkPassword"
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="doSignin"
          >注册</el-button
        >
      </el-form-item>
    </el-form>

    <!-- <el-dialog
      title="温馨提示"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <span>请输入账号和密码</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog> -->
  </div>
</template>

<script>
export default {
  name: "Signin",
  data() {
    return {
      form: {
        username: "",
        password: "",
        checkPassword: "",
        tel:"",
      },

      // 表单验证，需要在 el-form-item 元素中增加 prop 属性
      rules: {
        username: [
          { required: true, message: "账号不可为空", trigger: "blur" },
        ],
        password: [
          { required: true, message: "密码不可为空", trigger: "blur" },
        ],
        checkPassword: [
          { required: true, message: "密码不可为空", trigger: "blur" },
        ],
        tel: [
          { required: true, message: "手机号不可为空", trigger: "blur" },
        ],
      },

      // 对话框显示和隐藏
      dialogVisible: false,
    };
  },
  methods: {
    doSignin() {
      if (this.form.password != this.form.checkPassword) {
        alert("两次输入密码不一致!");
      } else {
        this.$http
          .request({
            method: "post",
            url: "/user/insertUser",
            data: {
              username: this.form.username,
              pass: this.form.password,
              tel: this.form.tel,
            },
          })
          .then((res) => {
              let {state} = res.data;
              if(state === 'success'){
                    this.$router.push('/');
              }else {
                  alert("账号已存在！");
              }
          });
      }
    },
    // onSubmit(formName) {
    //   // 为表单绑定验证功能
    //   this.$refs[formName].validate((valid) => {
    //     if (valid) {
    //       // 使用 vue-router 路由到指定页面，该方式称之为编程式导航
    //       this.$router.push("/main");
    //     } else {
    //       this.dialogVisible = true;
    //       return false;
    //     }
    //   });
    // }
  },
};
</script>

<style scoped>
.login-box {
  border: 1px solid #dcdfe6;
  width: 350px;
  margin: 180px auto;
  padding: 35px 35px 15px 35px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  box-shadow: 0 0 25px #909399;
}

.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
</style>